body, html{
  height: 100%;
}

body{
  padding: 0;
  margin: 0;
  color: $fontColor;
  background-color: $backgroundColor-presentation;
  overflow: scroll;
  width: 100vw;
  height: 100vh;
  position: relative;
}

section{
  position: relative;
  z-index: 100;
}

.content{
  width: 90%;
  max-width: 700px;
  margin: 0 auto;
  text-shadow: 0px 0px 5px  black;
}

p{
  line-height: $lineHeight;
  font-family: 'work sans';
  font-size: $fs-mobile-p;
  margin: 0;
  margin-bottom: $mt-18px;
}

a{
  line-height: $lineHeight;
  font-family: 'work sans';
  font-size: $fs-mobile-p;
  margin: 0;
  text-decoration: none;
}

h1{
  line-height: $lineHeight;
  font-family: 'forum';
  font-size: $fs-mobile-h2;
  margin: 0;
  margin-bottom: $mt-51px;
  margin-top: $mt-51px;
}

@media only screen and (min-width: 700px) {
  .content{
    width: 80%;
  }
  a{
    font-size: $fs-p;
  }
  p{
    font-size: $fs-p;
    margin-bottom: $mt-20px;
  }
  h1{
    font-size: $fs-h2;
    margin-bottom: $mt-56px;
    margin-top: $mt-56px;
  }
}

@media only screen and (min-width: 1200px){
    .content{
      margin-left: 20vh;
    } 
}