@import 'utils/variables';
@import 'components/type';

// fonts install
@font-face {
    font-family: 'Header font';
    src: url(../assets/fonts/ringbearer-font/RingbearerMedium-51mgZ.ttf);
}

@font-face {
    font-family: 'forum';
    src: url(../assets/fonts/Forum/Forum-Regular.ttf);
}

@font-face {
    font-family: 'work sans';
    src: url(../assets/fonts/Work_Sans/WorkSans-VariableFont_wght.ttf);
}

// introduction
.introduction{
    position: relative;
    z-index: 100;

    a{
        background-image: url(../assets/images/stage_nuage.png);
        background-size: 100% 100%;
        background-position: center;
        background-repeat: no-repeat;
        width: 30vw;
        height: 20vw;
        max-width: 200px;
        max-height: 130px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 5vh;
        color: $fontColor;
        z-index: 1000;
    
        &:hover{
            text-decoration: underline;
            cursor: pointer;
        }
        &:active{
            font-weight: bold;
        }
    }
}

// text
.text{
    margin-top: $mt-51px;
    p{
        margin-bottom: 30vh;

        &:last-child{
            margin-bottom: 50vh;
        }
    }
}

// assets
.assets{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    overflow: hidden;
    margin-top: 0;
    z-index: 1;

    &__anim__layer{
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: 100% 100%;
        background-position: center;
        background-repeat: no-repeat;
        transform: translateY(100vh);
        &:nth-child(1){
            background-image: url(../assets/images/background_plan_1.png);
            z-index: 15;
        }
        &:nth-child(2){
            background-image: url(../assets/images/background_plan_2.png);
            z-index: 10;
        }
        &:nth-child(3){
            background-image: url(../assets/images/background_plan_3.png);
            z-index: 5;
        }
        &:nth-child(4){
            width: 10vh;
            height: 15vh;
            bottom: 1vh;
            right: calc(50vw - 5vh);
            background-image: url(../assets/images/gandalf_right.png);
            transform: translate(0);
            opacity: 0;
            z-index: 20;
        }
        &:nth-child(5){
            z-index: 25;
            animation: batAnim 0.3s infinite linear;
            transform: translate(100vw, -100vh);
            width: 15vw;
            height: 9vw;
            max-width: 85px;
            max-height: 54px;
            right: 20vw;
            bottom: 40vh;
        }
        &:nth-child(6){
            z-index: 25;
            animation: batAnim 0.3s infinite linear;
            transform: translate(100vw, -100vh);
            width: 15vw;
            height: 9vw;
            max-width: 85px;
            max-height: 54px;
            right: 30vw;
            bottom: 45vh;
        }
        &:nth-child(7){
            z-index: 25;
            animation: batAnim 0.3s infinite linear;
            transform: translate(100vw, -100vh);
            width: 15vw;
            height: 9vw;
            max-width: 85px;
            max-height: 54px;
            right: 15vw;
            bottom: 50vh;
        }
        &:nth-child(8){
            background-image: url(../assets/images/stage_nuage.png);
            width: 30vw;
            height: 20vw;
            max-width: 200px;
            max-height: 130px;
            top: 5vh;
            right: 10vw;
            z-index: 30;
            opacity: 0;
            transform: translate(0);
        }
    }
}

@keyframes batAnim {
    0%{
      background-image: url(../assets/images/bats.png);
    }
    50%{
      background-image: url(../assets/images/bats.png);
    }
    51%{
      background-image: url(../assets/images/bats_2.png);
    }
    100%{
      background-image: url(../assets/images/bats_2.png);
    }
  }